.wca-customizer-control--color {
  margin: 0; }
  .wca-customizer-control--color .delete {
    margin-left: auto;
    color: #dcdcdc;
    transition: .3s ease; }
    .wca-customizer-control--color .delete:hover {
      color: var(--wp-admin-theme-color); }
    .wca-customizer-control--color .delete + div {
      margin-left: 0; }
  .wca-customizer-control--color .clear {
    width: 100%;
    justify-content: center;
    border-radius: 0; }

.wca-color-component {
  display: flex;
  align-items: center; }
  .wca-color-component .customize-control-title {
    margin-bottom: 0; }
  .wca-color-component .components-dropdown:first-of-type {
    margin-left: auto; }
  .wca-color-component .components-dropdown > button {
    display: block;
    width: 30px;
    height: 30px;
    border: 1px solid lightgray !important;
    box-shadow: none;
    position: relative; }
    .wca-color-component .components-dropdown > button .color,
    .wca-color-component .components-dropdown > button .gradient {
      display: block;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      position: absolute; }
    .wca-color-component .components-dropdown > button .color {
      z-index: 1; }
    .wca-color-component .components-dropdown > button .gradient {
      background-image: linear-gradient(45deg, #fff 0%, #ccc 0%, #ccc 10%, #fff 10%, #fff 20%, #ccc 20%, #ccc 30%, #fff 30%, #fff 40%, #ccc 40%, #ccc 50%, #fff 50%, #fff 60%, #ccc 60%, #ccc 70%, #fff 70%, #fff 80%, #ccc 80%, #ccc 90%, #fff 90%, #fff 100%, #ccc 100%) !important; }
  .wca-color-component .components-popover__content {
    overflow: hidden; }
  .wca-color-component .components-circular-option-picker__option {
    border: 1px solid lightgray; }
    .wca-color-component .components-circular-option-picker__option:after {
      content: none; }
  .wca-color-component--allows-global > .components-dropdown:last-child {
    margin-left: 0; }
  .wca-color-component--allows-global > .components-dropdown > button {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    border: 1px solid #b7c0cc;
    box-shadow: none; }
    .wca-color-component--allows-global > .components-dropdown > button > .dashicons {
      width: auto; }
  .wca-color-component--allows-global .global-color-picker {
    color: #b7c0cc;
    border-right-color: transparent;
    margin-right: -1px; }
    .wca-color-component--allows-global .global-color-picker:hover {
      color: #b7c0cc; }
    .wca-color-component--allows-global .global-color-picker.active {
      color: var(--wp-admin-theme-color); }
